import React, { useEffect } from 'react';
import { Canvas } from './Canvas';

export interface NodeType {
    val: string | number;
}

export const Node: React.FC<NodeType> = (props) => {
    const { val } = props;

    useEffect(() => {
        var canvasNumber = document?.querySelectorAll('#myCanvas').length;
        const ctx: any = document?.getElementById('myCanvas')?.getContext('2d');
        ctx.beginPath();
        ctx.arc(canvasNumber + 75, canvasNumber + 75, 50, 0, Math.PI * 2, true);
        ctx.lineWidth = 2;
        ctx.stroke();
        ctx.font = '30px Arial';
        ctx.fillText(val, canvasNumber + 65, canvasNumber + 85);
    }, []);

    return (
        <>
            <Canvas></Canvas>
        </>
    );
};
